@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700|Roboto:400,500);
html,
body {
  font-size: 16px;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #081b23;
}
@keyframes changeColor {
  0% {
    height: 0;
    width: 0;
  }
  30% {
    border-width: 20px;
    opacity: 1;
  }
  100% {
    border-width: 0;
    height: 50px;
    opacity: 0;
    width: 50px;
  }
}

.App{display:flex;flex-direction:column;min-height:100vh;opacity:1 !important;position:relative}.Head{min-height:100vh}.moviePageWrapper{background-color:#081b23;min-height:100vh}*{font-family:"Roboto", sans-serif}h2{font-size:20px}h1,h2,h3,h4,h5,h6{font-family:"Montserrat", sans-serif;font-weight:700;color:#e3f4fc}@media (min-width: 992px){h2{font-size:28px}}p,span{color:#9fbbc7}.primaryButton{color:#ffffff;border:0;padding:12px 23px;transition:all 0.3s ease;background-color:#0f4d69}.primaryButton:hover{background-color:#0d3f57}.uppercase{text-transform:uppercase}.pointer{cursor:pointer}@media (min-width: 992px){.header-wrapper{background-image:none !important;background-size:contain;height:80px}}.sectionTileRow{margin-top:40px;margin-bottom:20px}@media (min-width: 992px){.sectionTileRow{margin-top:75px;margin-bottom:40px}}.navigationRow{padding-bottom:30px}.navigationRow .prev-btn{float:left}.navigationRow .next-btn{float:right}.movieThumbWrapper{margin-bottom:24px}.movieThumbWrapper .movie-thumb-img{border-radius:10px}.movieThumbWrapper .movie-name{font-size:16px;color:#e6f7ff;margin-top:12px;margin-bottom:3px}.movieThumbWrapper .release-date{font-size:14px;color:#a1d1e6}.movieThumbWrapper .thumb-img-wrapper{position:relative;transition:all 0.3s ease}.movieThumbWrapper .thumb-img-wrapper:hover{transform:translateY(-2%)}.movieThumbWrapper .rating-badge{position:absolute;color:#fff;font-weight:500;font-size:14px;padding:3px 12px;left:5px;top:5px;border-radius:12px}.movieThumbWrapper .rating-badge.high-rating{background-color:#01d277}.movieThumbWrapper .rating-badge.medium-rating{background-color:#4902a3}.movieThumbWrapper .rating-badge.low-rating{background-color:#d1225b}.movie-header-wrapper{height:224px;background-size:cover;background-position:center center;position:relative}.movie-pg-desktop-title{font-size:45px;color:#081b23;background-color:#fff;padding:15px 35px;display:inline-block;position:relative}.movie-pg-desktop-title:after{content:"";display:block;position:absolute;width:95%;height:20px;background-color:#01d277;bottom:-10px}@media (min-width: 992px){.movie-pg-desktop-title{font-size:60px;color:#081b23;background-color:#fff;padding:30px 60px;display:inline-block;position:relative}.movie-pg-desktop-title:after{content:"";display:block;position:absolute;width:95%;height:30px;background-color:#01d277;bottom:-15px}}@media (min-width: 768px){.movie-header-wrapper{height:350px;display:flex;align-items:center;background-color:#0f303d}}@media (min-width: 1200px){.movie-header-wrapper{height:300px;background-color:#0f303d}}.desktop-movie-details{background-color:rgba(255,255,255,0.05);padding-top:20px;padding-bottom:20px}.desktop-movie-details p,.desktop-movie-details span{color:#fff;opacity:0.9}.desktop-movie-details .details-divider{color:#01d277;padding:0 20px;opacity:1}.details-divider{color:#01d277;padding:0 20px}.movie-back-arrow{position:absolute;top:30px;left:15px}.movie-pg-poster{box-shadow:0px 4px 20px rgba(0,0,0,0.5),0px 8px 16px rgba(0,0,0,0.5),0px 16px 32px rgba(0,0,0,0.5);border-radius:10px;margin-top:-30%}@media (min-width: 768px){.movie-pg-poster{margin-top:60px}}.movie-pg-title{margin-top:20px;font-size:25px}.movie-details{margin-top:15px}.movie-details p{font-size:12px;font-family:"Montserrat", sans-serif;color:#b8d8e6;margin-bottom:3px}.movie-pg-section-divider{margin-top:30px;border:0.5px solid #0f303d}.movie-overview-title{font-size:20px;margin-top:20px;margin-bottom:20px}@media (min-width: 768px){.movie-overview-title{margin-top:60px}}.movie-description{font-size:16px;line-height:24px}

